h1 {
  text-align: center;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: gray;
}

button {
  color: #f0f8ff;
  background-color: #4caf50;
  border: none;
  border-radius: 40px;
  margin-top: 20%;
  margin-left: 50%;
  padding: 14px 40px;
  font-family: Dela Gothic One, cursive;
  font-size: 72px;
  font-weight: 700;
  transition: all .5s;
  transform: translate(-50%, -50%);
  box-shadow: 0 5px 15px rgba(11, 99, 246, .7);
}

button:hover {
  transform: translate(-51%, -60%);
  box-shadow: 0 20px 40px rgba(11, 99, 246, .3);
}

